nav[aria-label = "breadcrumb"]{
  --bs-breadcrumb-divider: ">";

  li{
    margin: initial;
  }
}

.list-group-item{
  width: 500px;
  margin-top: 0;

  a{
    color: inherit;
    text-decoration: 1px underline var(--bs-gray);
  }
}

.card{
  width: 18rem;
  margin: 1rem;
  border-radius: 0.25rem;
  box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.15);

  a:not(.decorate){
    display: inline-block;
    width: 100%;
    height: 100%;
    text-decoration: none;
  }

  div.card-body{

    small.text-muted{
      display: block;
      width: 100%;
      margin: 0.25rem;
      text-align: right;
    }
  }
}

.carousel .carousel-inner .carousel-item{

  img{
    filter: blur(3px);
  }

  .carousel-caption{
    background-color: #fffa;
    border-radius: 5px;
  }
}

.btn.btn-primary.btn-custom{
  margin: 4px;

  i{
    display: inline-block;
    margin-right: 8px;
  }
}
